import React, { Component } from "react";
import { wait, S } from "../dependencies";
import { userData } from "../Data";
import gsap from "gsap/gsap-core";

export default class Measures extends Component {
  constructor() {
    super();
    this.state = {
      bmr: this.HarrisBenedictBMR(),
      ibw: this.ibw("broca"),
      lbm: this.lbm("boer"),
    };
  }

  componentDidMount() {
    window.scrollTo({ top: 0 });
    this.bmiArrowAnim();
    this.forMoreInfoAnimation();

    this.centerSelect("bmrSelect");
    this.centerSelect("ibwSelect");
    this.centerSelect("lbmSelect");

    document.getElementById("measuresPage").style.opacity = "1";
  }

  bmi = () => userData.userInfo.weight / Math.pow(userData.userInfo.height / 100, 2);

  bmiArrowAnim = () => {
    const bmi = this.bmi();
    const arrow = document.getElementById("bmiArrow");
    if (bmi > 35) {
      gsap.to(arrow, { duration: 0.7, left: "calc(100% - 7px)", delay: 0.3 });
    } else if (bmi < 0) {
      arrow.style.left = "calc(0% - 7px)";
    } else {
      const bmibar = Math.round((bmi * 100) / 35);
      gsap.to(arrow, { duration: 0.7, left: `calc(${bmibar}% - 7px)`, delay: 0.3 });
    }
  };

  bmiRange = () => {
    const bmi = this.bmi();
    const age = userData.userInfo.age;
    let range, goal, color;

    if (bmi < 18.5 && age >= 20) {
      range = "نقص وزن";
      goal = "عليك اكتساب بعض الوزن";
      color = "#d0a50b";
    } else if (bmi >= 18.5 && bmi < 24.9 && age >= 20) {
      range = "وزن طبيعي";
      goal = "عليك الحفاظ على وزنك";
      color = "#40ff45";
    } else if (bmi >= 24.9 && bmi < 29.9 && age >= 20) {
      range = "وزن زائد";
      goal = "عليك خسارة بعض الوزن";
      color = "#d0a50b";
    } else if (bmi >= 29.9 && age >= 20) {
      range = "بدانة";
      goal = "عليك خسارة الوزن";
      color = "#dc2900";
    } else if (age <= 18) {
      range = "طفل أو مراهق";
    }

    return [range, goal, color];
  };

  HarrisBenedictBMR = () => {
    const gender = userData.userInfo.gender;
    const weight = userData.userInfo.weight;
    const height = userData.userInfo.height;
    const age = userData.userInfo.age;
    const bmr =
      gender === "male"
        ? 66.47 + 13.75 * weight + 5.003 * height - 6.755 * age
        : 655.1 + 9.563 * weight + 1.85 * height - 4.676 * age;
    return Math.round(bmr);
  };

  MifflinStJeorBMR = () => {
    const gender = userData.userInfo.gender;
    const weight = userData.userInfo.weight;
    const height = userData.userInfo.height;
    const age = userData.userInfo.age;
    const bmr = gender === "male" ? 10 * weight + 6.25 * height - 5 * age + 5 : 10 * weight + 6.25 * height - 5 * age - 161;
    return Math.round(bmr);
  };

  activityMultipier = () => Math.round(this.state.bmr * userData.userInfo.activity);

  ibw = (eq) => {
    const gender = userData.userInfo.gender;
    const height = userData.userInfo.height;
    let ibw;
    switch (eq) {
      case "broca":
        ibw = gender === "male" ? height - 100 - (height - 100) * 0.1 : height - 100 + (height - 100) * 0.15;
        break;
      case "devine":
        ibw = gender === "male" ? 50 + (height / 2.54 - 60) * 2.3 : 45.5 + (height / 2.54 - 60) * 2.3;
        break;
      case "robinson":
        ibw = gender === "male" ? 52 + (height / 2.54 - 60) * 1.9 : 49 + (height / 2.54 - 60) * 1.7;
        break;
      case "millier":
        ibw = gender === "male" ? 56.2 + (height / 2.54 - 60) * 1.41 : 53.1 + (height / 2.54 - 60) * 1.36;
        break;
      case "hamwi":
        ibw = gender === "male" ? 48 + (height / 2.54 - 60) * 2.7 : 45.5 + (height / 2.54 - 60) * 2.2;
        break;
      case "lemmens":
        ibw = 22 * Math.pow(height / 100, 2);
        break;

      default:
        break;
    }
    return Math.round(ibw);
  };

  lbm = (eq) => {
    const gender = userData.userInfo.gender;
    const height = userData.userInfo.height;
    const weight = userData.userInfo.weight;
    let lbm;
    switch (eq) {
      case "boer":
        lbm = gender === "male" ? 0.407 * weight + 0.267 * height - 19.2 : 0.252 * weight + 0.473 * height - 48.3;
        break;
      case "james":
        lbm =
          gender === "male"
            ? 1.1 * weight - 128 * Math.pow(weight / height, 2)
            : 1.07 * weight - 148 * Math.pow(weight / height, 2);
        break;
      case "hume":
        lbm = gender === "male" ? 0.3281 * weight + 0.33929 * height - 29.5336 : 0.29569 * weight + 0.41813 * height - 43.2933;
        break;

      default:
        break;
    }
    const lbmP = Math.round((lbm * 100) / weight);
    const lbmF = Math.round(100 - lbmP);
    return [Math.round(lbm), lbmP, lbmF];
  };

  tbw = () => {
    const gender = userData.userInfo.gender;
    const weight = userData.userInfo.weight;
    const height = userData.userInfo.height;
    const age = userData.userInfo.age;
    const tbw =
      gender === "male" ? 2.447 - 0.09156 * age + 0.1074 * height + 0.3362 * weight : -2.097 + 0.1069 * height + 0.2466 * weight;
    return Math.round(tbw);
  };

  centerSelect = async (id) => {
    const element = document.getElementById(id);
    const newDiv = document.createElement("p");
    newDiv.classList.add("centerSelect")
    newDiv.innerHTML = element.options[element.selectedIndex].innerHTML;
    document.body.appendChild(newDiv);
    const getWidth = parseFloat(window.getComputedStyle(newDiv).getPropertyValue("width")) / 2 + "px)";
    newDiv.remove();
    
    element.style.width = "0px";
    await wait(20);
    element.style.removeProperty("width");
    element.style.paddingLeft = "calc(50% - " + getWidth;
  };

  infoHandle = async (cardName) => {
    gsap.to(`#${cardName}Card`, { duration: 0.15, scaleY: 0 });
    gsap.to(`#${cardName}Card`, { duration: 0.15, delay: 0.15, scaleY: 1 });
    await wait(150);
    S(`#${cardName}Card > *`).css("display", "none");
    S(`#${cardName}Card > .measureInfoClose`).css("display", "block");
    S(`#${cardName}Card > .infoPara`).css("display", "block");
  };

  infoClosehandle = async (cardName) => {
    gsap.to(`#${cardName}Card`, { duration: 0.15, scaleY: 0 });
    gsap.to(`#${cardName}Card`, { duration: 0.15, delay: 0.15, scaleY: 1 });
    await wait(150);
    S(`#${cardName}Card > *`).removeAttr("style");
    S(`#${cardName}Card > .measureInfoClose`).removeAttr("style");
    S(`#${cardName}Card > .infoPara`).removeAttr("style");
  };

  forMoreInfoAnimation = () => {
    const getOriginalWidth = S("#forMoreInfo").getStyle("width");
    const getWidth  = window.innerWidth <= 400 ? "160px" : "180px";
    const tl = gsap.timeline({ delay: 1.5 });
    tl.to("#forMoreInfo", { duration: 0.3, scale: 1 }, "a")
      .to("#bmiCard > .measureInfo", { duration: 0.3, fill: "white" }, "a")
      .to("#forMoreInfo", { duration: 0.3, width: getWidth })
      .to("#forMoreInfo", { duration: 0.3, width: getOriginalWidth }, "+=3")
      .to("#forMoreInfo", { duration: 0.3, scale: 0 }, "+=0.2")
      .to("#bmiCard > .measureInfo", { duration: 0.3, clearProps: "fill" }, "-=0.5");
  };

  render() {
    return (
      <div id="measuresPage">
        <header id="measuresPageHeader">
          <p id="measuresPageHeaderTitle">حاسبة مقاييس الجسم</p>
        </header>
        <div className="cardContainer" id="bmiCard">
          <div id="forMoreInfo">
            <p id="forMoreInfoPara">لمزيد من المعلومات</p>
          </div>

          <svg
            className="measureInfo"
            onClick={() => this.infoHandle("bmi")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <svg
            className="measureInfoClose"
            onClick={() => this.infoClosehandle("bmi")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <p className="mainTitles">مؤشر كتلة الجسم ( BMI )</p>
          <p className="subTitles">مؤشر كتلة جسمك</p>
          <p className="results">{this.bmi().toFixed(1)}</p>
          <p className="subTitles" style={{ color: this.bmiRange()[2] }}>
            {this.bmiRange()[0]}
          </p>
          <div id="bmibar">
            <span id="bmiArrow"></span>
          </div>
          <p className="subTitles">{this.bmiRange()[1]}</p>
          <p className="infoPara">
            <span>مؤشر كتلة الجسم</span>
            <br />
            يمكن لمؤشر كتلة الجسم إخبارك بالوزن الزائد ولكن لا يمكن أن يكون معياراً دقيقاً لاخبارك بوزن الدهون الزائدة لأنه لا
            يميز الفرق بين الوزن الزائد للعضلات أو للدهون أو للعظام . على عكس الأطفال ، مؤشر كتلة الجسم للبالغين لا يتأثر بالعمر
            أو الجنس أو الكتلة العضلية. الحمل يؤثر أيضاً على مؤشر كتلة الجسم لهذا السبب يجب استخدام الوزن ما قبل الحمل لحساب مؤشر
            كتلة الجسم.
          </p>
        </div>
        <div className="cardContainer" id="bmrCard">
          <svg
            className="measureInfo"
            onClick={() => this.infoHandle("bmr")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <svg
            className="measureInfoClose"
            onClick={() => this.infoClosehandle("bmr")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <p className="mainTitles">الاستقلاب الأساسي ( BMR )</p>
          <div className="selectContainer">
            <p className="selectTitle">اختر المعادلة</p>
            <select
              id="bmrSelect"
              className="selectEquation"
              onChange={(e) => {
                e.target.value === "Harris"
                  ? this.setState({ bmr: this.HarrisBenedictBMR() })
                  : this.setState({ bmr: this.MifflinStJeorBMR() });
                this.centerSelect("bmrSelect");
              }}
            >
              <option value="Harris">Harris Benedict Equation</option>
              <option value="Mifflin">Mifflin-St Jeor Equation</option>
            </select>
          </div>
          <p className="subTitles">معدل استقلابك الأساسي</p>
          <p className="results">
            {this.state.bmr}
            <span className="resultsUnits">سعرة في اليوم</span>
          </p>
          <p className="subTitles">السعرات الحرارية اللازمة يومياً</p>
          <p className="results">
            {this.activityMultipier()}
            <span className="resultsUnits">سعرة في اليوم</span>
          </p>
          <p className="infoPara">
            <span>معدل الاستقلاب الأساسي</span>
            <br />
            معدل الأيض الأساسي أو معدل الاستقلاب الأساسي هو القيمة التي تستخدم لوصف الاستقلاب (الأيض) ، وهي قيمة الطاقة التي
            يتطلبها الجسم خلال يوم واحد لإعادة صيانة وظائفه عندما يكون الجسم في حالة راحة تامة وفي حالة اليقظة صباحاً وفي حالة عدم
            نشاط عملية الهضم وتحت درجة الحرارة العادية للغرفة ( 28 درجة مئوية). كما توفر لك الحاسبة امكانية حساب السعرات اليومية
            اللازمة للحفاظ على الوزن وذلك بناءً على نشاطك اليومي.
          </p>
        </div>
        <div className="cardContainer" id="ibwCard">
          <svg
            className="measureInfo"
            onClick={() => this.infoHandle("ibw")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <svg
            className="measureInfoClose"
            onClick={() => this.infoClosehandle("ibw")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <p className="mainTitles">وزن الجسم المثالي ( IBW )</p>
          <div className="selectContainer">
            <p className="selectTitle">اختر المعادلة</p>
            <select
              id="ibwSelect"
              className="selectEquation"
              onChange={(e) => {
                this.setState({ ibw: this.ibw(e.target.value) });
                this.centerSelect("ibwSelect");
              }}
            >
              <option value="broca">The Borca Index</option>
              <option value="devine">The Davine Formula</option>
              <option value="robinson">Robinson Formula</option>
              <option value="millier">The Miller Formula</option>
              <option value="hamwi">The Hamwi Formula</option>
              <option value="lemmens">Lemmens Formula</option>
            </select>
          </div>
          <p className="subTitles">الوزن المثالي لجسمك</p>
          <p className="results">
            {this.state.ibw}
            <span className="resultsUnits">كيلوغرام</span>
          </p>
          <p className="infoPara">
            <span>الوزن المثالي للجسم</span>
            <br />
            يشير (IBW) إلى الوزن المثالي للجسم استناداً إلى جنسك وطولك. تم تقديم (IBW) في الواقع لتقدير الجرعات للاستخدام الطبي ،
            ولا يتعلق على الإطلاق بالجاذبية البصرية المتصورة في المجتمع. اليوم يتم استخدام (IBW) على نطاق واسع في المجالات
            الرياضية ، حيث يتم تصنيف الرياضيين بناءً على وزن الجسم. لاحظ أن (IBW) ليس مقياساً مثالياً. فالمعادلات لا تأخذ في
            الاعتبار النسب المئوية للدهون والعضلات في الجسم. هذا يعني أنه من الممكن للرياضيين الذين يتمتعون بلياقة عالية وصحة جيدة
            أن يعتبروا يعانون من زيادة الوزن بناءً على (IBW). لهذا السبب يجب النظر إلى (IBW) من منظور أنه مقياس غير كامل وليس
            بالضرورة مؤشراً على الصحة ، أو وزناً يجب أن يسعى الشخص لتحقيقه بالضرورة. يتم استخدام صيغ مختلفة لحساب (IBW). يمكنك
            تغيير الصيغة من القائمة المنسدلة.
          </p>
        </div>
        <div className="cardContainer" id="lbmCard">
          <svg
            className="measureInfo"
            onClick={() => this.infoHandle("lbm")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <svg
            className="measureInfoClose"
            onClick={() => this.infoClosehandle("lbm")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <p className="mainTitles">كتلة عضلات الجسم ( LBM )</p>
          <div className="selectContainer">
            <p className="selectTitle">اختر المعادلة</p>
            <select
              id="lbmSelect"
              className="selectEquation"
              onChange={(e) => {
                this.setState({ lbm: this.lbm(e.target.value) });
                this.centerSelect("lbmSelect");
              }}
            >
              <option value="boer">The Boer Formula</option>
              <option value="james">The James Formula</option>
              <option value="hume">The Hume Formula</option>
            </select>
          </div>
          <p className="subTitles">كتلة العضلات في جسمك</p>
          <p className="results">
            {this.state.lbm[0]}
            <span className="resultsUnits">كيلوغرام</span>
          </p>
          <p className="subTitles">نسبة كتلة العضلات في جسمك</p>
          <p className="results">
            {this.state.lbm[1]}
            <span className="resultsUnits"> % </span>
          </p>
          <p className="subTitles">نسبة كتلة الدهون في جسمك</p>
          <p className="results">
            {this.state.lbm[2]}
            <span className="resultsUnits"> % </span>
          </p>
          <p className="infoPara">
            <span>كتلة العضلات في الجسم</span>
            <br />
            تقوم حاسبة (LBM) بحساب كتلة العضلات في الجسم المقدرة للشخص بناءً على وزن الجسم والطول والجنس والعمر. لأغراض المقارنة ،
            توفر الآلة الحاسبة معادلات متعددة.
          </p>
        </div>
        <div className="cardContainer" id="tbwCard">
          <svg
            className="measureInfo"
            onClick={() => this.infoHandle("tbw")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M11 7h2v2h-2zm0 4h2v6h-2zm1-9C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <svg
            className="measureInfoClose"
            onClick={() => this.infoClosehandle("tbw")}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path d="M14.59 8L12 10.59 9.41 8 8 9.41 10.59 12 8 14.59 9.41 16 12 13.41 14.59 16 16 14.59 13.41 12 16 9.41 14.59 8zM12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z" />
          </svg>
          <p className="mainTitles">حجم مياه الجسم ( TBW )</p>
          <p className="subTitles">الحجم الكلي للمياه في جسمك</p>
          <p className="results">
            {this.tbw()}
            <span className="resultsUnits">لتر</span>
          </p>
          <p className="infoPara">
            <span>الحجم الكلي للمياه في الجسم</span>
            <br />
            تستخدم حاسبة حجم المياه الكلي في الجسم العمر والطول والوزن والجنس لتقدير حجم الماء في جسمك. بناءً على صيغة وضعها
            الدكتور واتسون وفريقه والتي وصفت في عام 1980.
          </p>
        </div>
      </div>
    );
  }
}
